<script>
  import Dexie from 'dexie';

  export default {
    name: 'HelloWorld',

    data: () => ({
      dexie_db: null,
      Sire_data: "",
      mySire:[],
      Dam_data: "",
      myDam:[],
      sire_pedigree_active: true,
      dam_pedigree_active: true,


    }),
    methods:{

      gettime(){
        var now = new Date();
        var timestamp = 
          now.getFullYear() + '/' + (now.getMonth()+1) + '/' + now.getDate()
          + '  ' + ('0'+now.getHours()).slice(-2) + ':' + ('0' + now.getMinutes()).slice(-2); 
        return timestamp;
      },

      submit(){
      },

      erase(){
        this.dexie_db.MsgDB.clear();
        this.initializer();
      },

      dbinitializer(){
        this.dexie_db = new Dexie('DB-PD-MAKER');
        this.dexie_db.version(1).stores({
          PdStore: 'dict_name,dict_content'
        });
        fetch('./json/default_pedigree.json')
          .then(response => {
              return response.json();
          })
          .then(json => {
              this.dexie_db.PdStore.put({dict_name:"sire_data",dict_content:json.colt_data});
              //this.dexie_db.MsgDB.put({dict_name:"dam_data",dict_content:json.filly_data});
          });

        this.dexie_db.PdStore.get("sire_data")
          .then(data=>{
            this.mySire=data.dict_content
          })
        
      },


      initializer(){
        this.dbinitializer();
      }
    },    
    mounted(){
        this.initializer();
    },

  }
</script>

